<template>
	<div>
		<HeaderComponent :view-title="helpTitle"></HeaderComponent>
		<div id="help-container">
			<section class="selectList" v-for="(item,index) in helpData" @click.stop="toggle(index,item,$event)">
				<div class="mainTittle">
					<span>{{item.title}}</span>
					<svg class="iconHelp" aria-hidden="true" v-show="!item.show">
	  					<use xlink:href="#icon-jiantouxia"></use>
					</svg>
					<svg class="iconHelp iconHelpTop" aria-hidden="true" v-show="item.show">
	  					<use xlink:href="#icon-jiantoushang"></use>
					</svg>
				</div>
				<!-- <ul class="subTittle" v-show="item.show">
					<li>{{item.msg}}</li>
				</ul> -->
				<transition name="fade">
					<ul class="subTittle" v-show="item.show">
						<li>{{item.msg}}</li>
					</ul>
				</transition>
			</section>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		helpTitle:Object,
		helpData:Object,
		data(){
			return{
				helpTitle:{
					name:'帮助中心',
					showCode:1
				},
				currentIndex:String,
				status:false,
				helpData:[
					{title:'如何创建一场抽奖活动？',msg:'编辑活动时，正在进行的活动将暂停保存后再发布；编辑活动时，正在进行的活动将暂停保存后再发布。',show:false},
					{title:'如何创建一场抽奖活动？',msg:'编辑活动时，正在进行的活动将暂停保存后再发布；编辑活动时，正在进行的活动将暂停保存后再发布。',show:false},
					{title:'如何创建一场抽奖活动？',msg:'编辑活动时，正在进行的活动将暂停保存后再发布；编辑活动时，正在进行的活动将暂停保存后再发布。',show:false},
					{title:'编辑我的活动时是怎样的？',msg:'编辑活动时，正在进行的活动将暂停保存后再发布；编辑活动时，正在进行的活动将暂停保存后再发布。',show:false},
					{title:'如何创建一场抽奖活动？',msg:'编辑活动时，正在进行的活动将暂停保存后再发布；编辑活动时，正在进行的活动将暂停保存后再发布。',show:false}
				]
			}
		},
		mounted(){},
		methods:{
			toggle:function(index,item,e){
				this.helpData.forEach(data => {
					if(data.show !== this.helpData[index].show){
						data.show = false;
					}
				});
				item.show = !item.show;
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconHelp {
	   width: 0.9rem; height: 0.9rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#A1A1A1;
	   position:absolute;
	   right:0;
	   top:0.9rem;
	}
	#help-container{
		width:100%;
		margin-top:2.45rem;
		.selectList{
			width:100%;
			min-height: 0;	
			overflow: hidden;
			padding:0 0.5rem;
			background:#fff;
			&:not(:last-child){
				border-bottom:1px solid #D9D9D9;
			}
			.mainTittle{
				width:100%;
				height:2.7rem;
				line-height:2.7rem;
				position:relative;
				span{
					@include sc(0.7rem,#333);
				}
			}
			.subTittle{
				li{
					@include sc(0.7rem,#ACACAC);
					padding:0.3rem 0;
					transition: display 2s linear;	
				}
			}
		}
	}
	/* .fade-enter-active, .fade-leave-active {
	  		transition: opacity .5s;
	  		transition: all .3s ease;
	}
	.fade-enter, .fade-leave-to .fade-leave-active below version 2.1.8 {
	  opacity: 0;
	} */
</style>


